import React, { useState } from "react";
import { Layout, Menu, Drawer, Icon, Button } from "antd";

import { Mobile, Default } from "../Responsive";

import "./index.less";

const { Header } = Layout;

const Navbar = props => {
  const [visible, setVisible] = useState(false);
  const menu = (
    <Menu
      theme="dark"
      mode={visible ? "vertical" : "horizontal"}
      defaultSelectedKeys={["1"]}
      style={{ lineHeight: "64px" }}
    >
      <Menu.Item key="1">Antd</Menu.Item>
      <Menu.Item key="2" disabled>
        custom-2
      </Menu.Item>
      <Menu.Item key="3" onClick={props.resetTheme}>
        
      </Menu.Item>
    </Menu>
  );

  return (
    <Header className="app-header">
      <div className="logo"> ~antd@3.20.5</div>

      <Button
        type="primary"
        shape="round"
        icon="download"
        onClick={() => {
          props.downloadFn();
        }}
        style={{ marginRight: 12 }}
      >
        
      </Button>
      <Button
        shape="round"
        icon="download"
        onClick={() => {
          props.downloadFn("default");
        }}
      >
        
      </Button>
      <Default>{menu}</Default>
      <Mobile>
        <Icon
          type="bars"
          size="large"
          className="nav-icon"
          onClick={() => setVisible(true)}
        />
        <Drawer
          title=""
          placement="right"
          closable
          onClose={() => setVisible(false)}
          visible={visible}
          className="nav-drawer"
        >
          <div className="logo"></div>
          {menu}
        </Drawer>
      </Mobile>
    </Header>
  );
};

export default Navbar;
